import Mock from 'mockjs'
import qs from 'qs'

// 基本配置
Mock.setup({
  // 随机延时500-1000毫秒,模拟网络延迟
  timeout: '500-1000'
})

// 拦截接口 /my/test
// 1.接口地址路径规则，需要匹配到它
// 2.请求方式
// 3.返回数据（函数返回数据）
Mock.mock(/\/my\/test/, 'get', () => {
  // 随机数据逻辑 目标：5条数据  [{id:'',name:''},...]
  const arr = []
  for (let i = 0; i < 5; i++) {
    // 模拟id
    // arr.push(Mock.mock('@id'))
    arr.push(Mock.mock({
      id: '@id',
      name: '@cname',
      image: '@image(200x100)'
    }))
  }
  return { msg: '获取数据成功', result: [arr] }
})

// 模拟我的收藏
Mock.mock(/\/member\/collect/, 'get', (config) => {
  // 地址上的需要查询的前端传过来的参数 0是前面的请求地址 1是后面的参数
  const queryString = config.url.split('?')[1]
  const queryObject = qs.parse(queryString)
  console.log('修改后的地址参数-对象', queryObject)
  const item = []
  for (let i = 0; i < +queryObject.pageSize; i++) {
    item.push(Mock.mock({
      id: '@id',
      name: '@ctitle(10,20)',
      description: '@ctitle(4,10)',
      price: '@float(100,200,2,2)',
      picture: `http://zhoushugang.gitee.io/erabbit-client-pc-static/uploads/clothes_goods_${Mock.mock('@integer(1,4)')}.jpg`
    }))
  }
  return {
    msg: '获取数据成功',
    result: {
      counts: 35,
      pageSize: +queryObject.pageSize,
      page: +queryObject.page,
      items: item
    }
  }
})

