<template>
  <div class="xtx-pagination">
    <a
      @click="changePager(myCurrentPage - 1)"
      v-if="myCurrentPage > 1"
      href="javascript:;"
      >上一页</a
    >
    <a v-else href="javascript:;" class="disabled">上一页</a>
    <span v-if="pager.start > 1">...</span>
    <a
      @click="changePager(i)"
      href="javascript:;"
      v-for="i in pager.btnArr"
      :key="i"
      :class="{ active: i === myCurrentPage }"
      >{{ i }}</a
    >
    <span v-if="pager.end < pager.pageCount">...</span>
    <a
      @click="changePager(myCurrentPage + 1)"
      v-if="myCurrentPage < pager.pageCount"
      href="javascript:;"
      >下一页</a
    >
    <a v-else class="disabled" href="javascript:;">下一页</a>
  </div>
</template>
<script>
import { computed, ref, watch } from 'vue'
export default {
  name: 'XtxPagination',
  props: {
    // 总条数
    total: {
      type: Number,
      default: 100
    },
    // 每页条数
    pageSize: {
      type: Number,
      default: 10
    },
    // 当前页
    currentPage: {
      type: Number,
      default: 1
    }
  },
  setup (props, { emit }) {
    // 当前第几页
    const myCurrentPage = ref(10)
    // 按钮个数
    const count = ref(5)
    // 总条数
    const myTotal = ref(100)
    // 每页条数
    const myPageSize = ref(10)
    // 总页数=总条数/每页条数 向上取整
    // 其他数据（总页数，起始按钮，结束按钮，按钮数据）依赖上面的数据得到
    const pager = computed(() => {
      // 总页数 10
      const pageCount = Math.ceil(myTotal.value / myPageSize.value)
      // 起始按钮 结束按钮
      // 当前页是在中间的
      // 1.理想情况下：
      const offset = Math.floor(count.value / 2)// 2
      let start = myCurrentPage.value - offset
      let end = start + count.value - 1
      // 2.不是理想情况下：如果起始页码小于0
      if (start < 1) {
        start = 1
        end = (start + count.value - 1) > pageCount ? pageCount : (start + count.value - 1)
      }
      // 3.结束页码：大于总页数
      if (end > pageCount) {
        end = pageCount
        start = (end - count.value + 1) < 1 ? 1 : (end - count.value + 1)
      }
      // 按钮数组(起始到结束)
      const btnArr = []
      for (let i = start; i <= end; i++) {
        btnArr.push(i)// [1,2,3,...]
      }
      // 提供计算属性数据
      // console.log('按钮数组', btnArr)
      return { btnArr, start, end, pageCount }
    })
    // 监听props的变化，去更新内部的数据
    watch(props, () => {
      myTotal.value = props.total
      myPageSize.value = props.pageSize
      myCurrentPage.value = props.currentPage
    }, { immediate: true })
    // 切换分页的方法
    const changePager = (page) => {
      myCurrentPage.value = page
      // 通知父组件
      emit('current-change', page)
    }
    return {
      count,
      myCurrentPage,
      pager,
      changePager
    }
  }
}
</script>
<style scoped lang="less">
.xtx-pagination {
  display: flex;
  justify-content: center;
  padding: 30px;
  > a {
    display: inline-block;
    padding: 5px 10px;
    border: 1px solid #e4e4e4;
    border-radius: 4px;
    margin-right: 10px;
    &:hover {
      color: @xtxColor;
    }
    &.active {
      background: @xtxColor;
      color: #fff;
      border-color: @xtxColor;
    }
    &.disabled {
      cursor: not-allowed;
      opacity: 0.4;
      &:hover {
        color: #333;
      }
    }
  }
  > span {
    margin-right: 10px;
  }
}
</style>
