import { checkAllCart, findCartList, getNewCartGoods, mergeCart, insertCart, deleteCart, updateCart } from '@/api/cart'
// 购物车模块
export default {
  namespaced: true,
  state () {
    return {
      // 购物车商品列表
      list: []
    }
  },
  getters: {
    // 1.有效的商品列表
    validList (state) {
      // 有效商品： 库存大于0 stock    商品有效标识为true isEffective
      return state.list.filter(goods => goods.stock > 0 || goods.isEffective)
    },
    // 2.有效商品总件数
    validTotal (state, getters) {
      return getters.validList.reduce((p, c) => p + c.count, 0)
    },
    // 3.有效商品总金额
    validAmount (state, getters) {
      // return getters.validList.reduce((p, c) => p + c.nowPrice * 100 * c.count, 0) / 100
      return getters.validList.reduce((p, c) => p + Math.round(c.nowPrice * 100) * c.count, 0) / 100
    },
    // 无效商品列表
    inValidList (state) {
      return state.list.filter(goods => goods.stock <= 0 || !goods.isEffective)
    },
    // 已选商品列表(从有效的商品列表 找出 已选的商品)
    selectedList (state, getters) {
      return getters.validList.filter(item => item.selected === true)
    },
    // 已选商品总件数
    selectedTotal (state, getters) {
      return getters.selectedList.reduce((p, c) => p + c.count, 0)
    },
    // 已选商品总金额
    selectedAmount (state, getters) {
      return getters.selectedList.reduce((p, c) => p + Math.round(c.nowPrice * 100) * c.count, 0) / 100
    },
    // 是否全选(boolean)
    isCheckAll (state, getters) {
      return getters.validList.length > 0 && (getters.selectedList.length === getters.validList.length)
    }
  },
  mutations: {
    // 添加商品到购物车
    insertCart (state, payload) {
      // payload对象
      // 插入数据规则：
      // 1.先去找一下是否有相同商品
      // 2.如果有相同商品,查询数量，累加到payload上，再保存在最新位置,原来商品需要删除
      // 3.如果没有相同商品，保存在最新位置可以
      const sameIndex = state.list.findIndex(goods => goods.skuId === payload.skuId)
      // 没有找到值,-1
      if (sameIndex > -1) {
        const count = state.list[sameIndex].count
        payload.count += count
        // 删除原来
        state.list.splice(sameIndex, 1)
      }
      // 追加新的
      state.list.unshift(payload)
    },
    // 修改购物车
    updateCart (state, goods) {
      // goods商品信息:nowPrice stock isEffective
      // goods 商品对象的字段不固定,对象中有哪些字段，就改哪些字段，字段的值合理才进行修改
      // goods商品对象中必须要有skuId
      const updateGoods = state.list.find(item => item.skuId === goods.skuId)
      for (const key in goods) {
        if (goods[key] !== undefined && goods[key] !== null && goods[key] !== '') {
          updateGoods[key] = goods[key]
        }
      }
    },
    // 删除购物车商品
    deleteCart (state, skuId) {
      const index = state.list.findIndex(item => item.skuId === skuId)
      console.log(index)
      state.list.splice(index, 1)
    },
    // 设置购物车
    setCart (state, payload) {
      // payload为空数组，清空，有值的数组，设置
      state.list = payload
    }

  },
  actions: {
    // 合并购物车
    async mergeCart (ctx) {
      // 合并的参数
      const cartList = ctx.state.list.map(goods => {
        return {
          skuId: goods.skuId,
          selected: goods.selected,
          count: goods.count
        }
      })
      await mergeCart(cartList).then(() => {
        // 合并成功,清空本地购物车
        ctx.commit('setCart', [])
      })
    },
    // 修改规格
    updateCartSku (ctx, { oldSkuId, newSku }) {
      return new Promise((resolve, reject) => {
        if (ctx.rootState.user.profile.token) {
          // TODO 已登录
          // 1.找出旧的商品信息
          // 2.删除旧的商品数据
          // 3.原先商品的数量+新skuId
          // 4.添加新的商品
          const oldGoods = ctx.state.list.find(item => item.skuId === oldSkuId)
          deleteCart([oldGoods.skuId]).then(() => {
            return insertCart({ skuId: newSku.skuId, count: oldGoods.count })
          }).then(() => {
            return findCartList()
          }).then(data => {
            // console.log('设置:', data)
            ctx.commit('setCart', data.result)
            resolve()
          })
        } else {
          // 未登录
          // 1.找出旧的商品信息
          // 2.删除旧的商品数据
          // 3.根据新的sku信息和旧的商品信息，合并成一条新的购物车商品信息
          // 4.添加新的商品
          const oldGoods = ctx.state.list.find(item => item.skuId === oldSkuId)
          ctx.commit('deleteCart', oldSkuId)
          const { skuId, price: nowPrice, specsText: attrsText, inventory: stock } = newSku
          const newGoods = { ...oldGoods, skuId, nowPrice, attrsText, stock }
          ctx.commit('insertCart', newGoods)
          resolve()
        }
      })
    },
    // 批量删除
    batchDeleteCart (ctx, isClear) {
      return new Promise((resolve, reject) => {
        if (ctx.rootState.user.profile.token) {
          // TODO 已登录
          // 拿到所有的skuId集合
          const ids = ctx.getters[isClear ? 'inValidList' : 'selectedList'].map(item => item.skuId)
          deleteCart(ids).then(() => {
            // 更新列表请求
            return findCartList()
          }).then((data) => {
            ctx.commit('setCart', data.result)
            resolve()
          })
        } else {
          // 未登录
          // 找出选择的商品列表，遍历调用删除的mutations
          // isClear 为true 删除失效商品列表  否则删除选中的商品列表
          ctx.getters[isClear ? 'inValidList' : 'selectedList'].forEach(item => {
            ctx.commit('deleteCart', item.skuId)
          })

          resolve()
        }
      })
    },
    // 删除购物车
    deleteCart (ctx, payload) {
      // 单条删除 payload现在就是skuId
      return new Promise((resolve, reject) => {
        if (ctx.rootState.user.profile.token) {
          // TODO 已登录
          deleteCart([payload]).then(() => {
            // 更新列表请求
            return findCartList()
          }).then(data => {
            ctx.commit('setCart', data.result)
            resolve()
          })
        } else {
          // 未登录
          ctx.commit('deleteCart', payload)
          resolve()
        }
      })
    },
    // 加入购物车
    insertCart (ctx, payload) {
      return new Promise((resolve, reject) => {
        if (ctx.rootState.user.profile.token) {
          // 已登录
          const { skuId, count } = payload
          insertCart({ skuId, count }).then(() => {
            return findCartList()
          }).then(data => {
            ctx.commit('setCart', data.result)
            resolve()
          })
        } else {
          // 未登录
          ctx.commit('insertCart', payload)
          resolve()
        }
      })
    },
    // 获取商品列表
    findCart (ctx) {
      return new Promise((resolve, reject) => {
        if (ctx.rootState.user.profile.token) {
          //  已登录
          findCartList().then(data => {
            ctx.commit('setCart', data.result)
            resolve()
          })
        } else {
          // 未登录
          // 同时发送请求（有几件商品发几个请求）等所有请求成功，一并去修改本地的数据
          // Promise.all([promise数组]).then((dataList)=>{}) 同时发送请求，所有请求成功，得到所有成功结果
          // Promise.resolve() Promise.reject() new Promise()
          // Promise.race(promise数组).then((data)=>{})  同时发送请求，最快的请求成功，得到成功结果
          const promiseArr = ctx.state.list.map(goods => {
            return getNewCartGoods(goods.skuId)
          })
          // dataList成功结果的集合，数据顺序和promiseArr顺序一致，它有根据state.list而来
          Promise.all(promiseArr).then(dataList => {
            // console.log('购物车：', results)
            dataList.forEach((data, i) => {
              ctx.commit('updateCart', { skuId: ctx.state.list[i].skuId, ...data.result })
            })
            // 更新本地购物车
            // 调用resolve代表操作成功
            resolve()
          })
        }
      })
    },
    // 修改购物车商品(选择状态，数量)
    updateCart (ctx, payload) {
      return new Promise((resolve, reject) => {
        if (ctx.rootState.user.profile.token) {
          // TODO 已登录
          updateCart(payload).then(() => {
            return findCartList()
          }).then(data => {
            ctx.commit('setCart', data.result)
            resolve()
          })
        } else {
          // 未登录
          // payload skuId 可能有selected count
          ctx.commit('updateCart', payload)
          resolve()
        }
      })
    },
    // 修改购物车的商品全选/取消全选
    checkAllCart (ctx, selected) {
      return new Promise((resolve, reject) => {
        if (ctx.rootState.user.profile.token) {
          // TODO 已登录
          const ids = ctx.getters.validList.map(item => item.skuId)
          checkAllCart({ selected, ids }).then(() => {
            return findCartList()
          }).then(data => {
            ctx.commit('setCart', data.result)
            resolve()
          })
        } else {
          // 未登录
          // payload skuId 可能有selected count
          ctx.getters.validList.forEach(goods => {
            ctx.commit('updateCart', { skuId: goods.skuId, selected })
          })
          resolve()
        }
      })
    }
  }
}
