import { createRouter, createWebHashHistory, RouterView } from 'vue-router'
import store from '@/store'
import { h } from 'vue'

const routes = [
  // 一级路由布局容器
  {
    path: '/',
    component: () => import('@/views/Layout'),
    children: [
      {
        path: '/',
        component: () => import('@/views/home')
      },
      {
        path: '/category/:id',
        component: () => import('@/views/category/index')
      },
      {
        path: '/category/sub/:id',
        component: () => import('@/views/category/sub')
      },
      {
        path: '/product/:id',
        component: () => import('@/views/goods/index')
      },
      {
        path: '/cart',
        component: () => import('@/views/cart/index')
      },
      {
        path: '/member/checkout',
        component: () => import('@/views/member/pay/checkout')
      },
      {
        path: '/member/pay',
        component: () => import('@/views/member/pay/index')
      },
      {
        path: '/pay/callback',
        component: () => import('@/views/member/pay/result')
      },
      {
        path: ' / member',
        component: () => import('@/views/member/Layout'),
        children: [
          {
            path: '/member',
            component: () => import('@/views/member/home/index')
          },
          // {
          //   path: '/member/order',
          //   component: () => import('@/views/member/order/index')
          // },
          // {
          //   path: '/member/order/:id',
          //   component: () => import('@/views/member/order/detail')
          // }
          {
            path: '/member/order/',
            // vue3.0 需要有嵌套关系才能模糊匹配
            // 创建一个RouterView容器形成嵌套关系
            component: { render: () => h(<RouterView />) },
            children: [
              { path: '', component: () => import('@/views/member/order/index') },
              { path: ':id', component: () => import('@/views/member/order/detail') }
            ]
          }
        ]
      }
    ]
  },
  {
    path: '/login',
    component: () => import('@/views/login')
  },
  {
    path: '/login/callback',
    component: () => import('@/views/login/components/callback')
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes,
  scrollBehavior () {
    return { left: 0, top: 0 }
  }
})

// 前置导航守卫
router.beforeEach((to, from, next) => {
  // 判断登录状态,需要登录的路由，地址是以 /member 开头
  const { profile } = store.state.user
  if (!profile.token && to.path.startsWith('/member')) {
    // 未登录 encodeURIComponent 转编码
    return next('/login?redirectUrl=' + encodeURIComponent(to.fullPath))
  } else {
    next()
  }
})
export default router
