import axios from 'axios'
import store from '@/store'
import router from '@/router'

// 导出基准地址，原因：其他地方不是axios发送请求的地方用上基准地址
export const baseURL = 'https://apipc-xiaotuxian-front.itheima.net'

// 创建实例
const instance = axios.create({
  // axios的配置
  baseURL,
  timeout: 5000
})

// 请求拦截器
instance.interceptors.request.use(config => {
  // 拦截业务逻辑
  // 进行请求配置的修改
  // 如果本地有token就在头部携带token
  // 1.获取用户信息对象
  const { profile } = store.state.user
  // 2.判断是否有token
  if (profile.token) {
    // 3.设置token
    config.headers.Authorization = `Bearer ${profile.token}`
  }
  return config
}, err => {
  return Promise.reject(err)
})

// 响应拦截器
// res.data取出data数据，将来调接口的时候直接拿到的就是后台的数据
instance.interceptors.response.use(res => res.data
  , err => {
    // 401状态码，进入该函数
    if (err.response && err.response.status === 401) {
      // 1.清空本地无效的用户信息
      // 2.跳转到登陆页面
      // 3.跳转需要传参（当前路由地址）给登录页
      store.commit('user/setUser', {})
      // 当前路由地址
      // 1.如果在组件里：$route.fullPath
      // 2.js模块中：router.currentRoute是ref响应式数据，router.currentRoute.value.fullPath
      // fullPath 不能用特殊字符，有特殊字符的话会自动&分离，后面会被切除
      // 解决：使用encodeURIComponent转码
      const fullPath = encodeURIComponent(router.currentRoute.value.fullPath)
      router.push('/login?redirectUrl=' + fullPath)
    }
    return Promise.reject(err)
  })

// 导出请求工具函数
export default (url, method, submitData) => {
  // 负责发请求，请求地址，请求方式，提交的数据
  return instance({
    url,
    method,
    // 1.如果是get请求，需要使用params来传递submitDate
    // 2.如果不是get请求，需要使用data来传递submitDate
    // 动态插入属性
    // const a={name:100} a.name a['name'] a[10>9?'name':'age']   中括号可以写表达式
    // []设置一个动态的key
    // toLowerCase() 转换为小写
    // 区别：get接收的参数用对象包裹起来，post接收的参数直接使用不需要包裹
    [method.toLowerCase() === 'get' ? 'params' : 'data']: submitData
  })
}
