// 扩展vue原有的功能：全局组件，自定义指令，挂载原型方法，注意：没有全局过滤器
// 这就是插件

// vue2插件写法要素：导出一个对象，要有install函数，默认传入Vue构造函数，在Vue基础之上扩展
// vue3插件写法要素：导出一个对象，要有install函数，默认传入app实例，在app基础之上扩展
// import XtxSkeleton from './xtx-skeleton.vue'
// import XtxCarousel from './xtx-carousel.vue'
// import XtxMore from './xtx-more.vue'
import defaultImg from '@/assets/images/200.png'
// import XtxBread from './xtx-bread.vue'
// import XtxBreadItem from './xtx-bread-item.vue'

import Message from './Message'
import Confirm from './Confirm'

// context()有三个参数：1.目录路径；2.是否加载子目录；3.加载文件的匹配正则；
const importFn = require.context('./', false, /\.vue$/)

export default {
  install (app) {
    // 在app上进行扩展，app提供compnent(全局组件) directive(自定义指令)
    //  如果要挂载原型 app.config.globalProperties方式
    // 1.全局挂载组件（每个地方都可以调用这个组件了）
    // app.component(XtxSkeleton.name, XtxSkeleton)
    // app.component(XtxCarousel.name, XtxCarousel)
    // app.component(XtxMore.name, XtxMore)
    // app.component(XtxMore.name, XtxMore)
    // app.component(XtxBread.name, XtxBread)
    // app.component(XtxBreadItem.name, XtxBreadItem)

    // 全局挂载组件:根据keys函数批量注册
    importFn.keys().forEach(path => {
      // 导入组件
      const component = importFn(path).default
      // 进行注册
      app.component(component.name, component)
    })
    // 定义指令
    defindeDirective(app)
    // 挂载原型方法: 定义一个原型函数
    app.config.globalProperties.$message = Message
    app.config.globalProperties.$confirm = Confirm
  }
}

// 定义指令
const defindeDirective = (app) => {
  // 1.图片懒加载指令(指令的名字不需要加 v-lazy，使用时才是v-lazy)
  // 原理：先存储图片地址，但是不能放在src上，当图片进入可视区，将你存储的地址 设置 给图片元素即可（操作dom）
  app.directive('lazy', {
    // vue2.0如果想知道dom有没有创建好，钩子函数：inserted
    // vue3.0的指令拥有的钩子函数和组件的一样，使用指令的dom是否创建好，钩子函数：mounted
    // el需要设置指令的dom， binding是一个对象（binding.value是绑定的值：图片的地址）
    mounted (el, binding) {
      // 2.创建一个观察对象，来观察当前使用指令的元素
      const observe = new IntersectionObserver(([{ isIntersecting }]) => {
        if (isIntersecting) {
          // 进入可视区后应该停止观察
          observe.unobserve(el)
          // 3.把图片地址的值传给指令，然后在重新将v-lazy指令的值设置给el的src属性
          // 4.处理图片加载失败,error图片加载失败的事件，load图片加载成功的事件
          el.onerror = () => {
            el.src = defaultImg
          }
          el.src = binding.value
          // console.log('进入可视区', el)
        }
      }, {
        threshold: 0
      })
      observe.observe(el)
    }
  })
}
