<template>
  <div class="xtx-city" ref="target">
    <!--请选择配送地址框框  -->
    <div class="select" @click="toggle()" :class="{ active: visible === true }">
      <!-- 占位 -->
      <span v-if="!fullLocation" class="placeholder">{{ placeholder }}</span>
      <!-- 真正的值 -->
      <span v-else class="value">{{ fullLocation }}</span>
      <i class="iconfont icon-angle-down"></i>
    </div>
    <!-- 城市 -->
    <div class="option" v-if="visible">
      <div v-if="loading" class="loading"></div>
      <template v-if="!loading">
        <span
          @click="changeItem(item)"
          class="ellipsis"
          v-for="item in currList"
          :key="item.code"
          >{{ item.name }}</span
        >
      </template>
    </div>
  </div>
</template>
<script>
import { computed, reactive, ref } from 'vue'
import { onClickOutside } from '@vueuse/core'
import axios from 'axios'
export default {
  name: 'XtxCity',
  props: {
    fullLocation: {
      type: String,
      default: ''
    },
    placeholder: {
      type: String,
      default: '请选择配送地址'
    }

  },
  setup (props, { emit }) {
    // 显示隐藏的数据 visible(显示)
    const visible = ref(false)
    // 所有城市数据
    const allCityData = ref([])
    // 正在加载数据
    const loading = ref(false)

    // 提供打开或关闭的行为方法
    const open = () => {
      visible.value = true
      // 获取城市地区数据
      loading.value = true
      getCityData().then(data => {
        allCityData.value = data
        loading.value = false
      })
      // 清空之前选择的数据
      // 遍历对象清空
      for (const key in changeResult) {
        changeResult[key] = ''
      }
    }
    const close = () => {
      visible.value = false
    }
    // 提供一个切换函数给select使用
    const toggle = () => {
      // 你是打开的，点击就关闭
      visible.value ? close() : open()
      // console.log(visible.value)
    }
    // 点击其他位置隐藏
    const target = ref(null)
    onClickOutside(target, () => {
      close()
    })
    // 实现计算属性：获取当前要显示的地区列表数组
    const currList = computed(() => {
      // 默认显示省一级
      let list = allCityData.value
      // 当你点击省级信息的时候,显示市一级的信息
      if (changeResult.provinceCode && changeResult.provinceName) {
        // areaList省下面的地区
        list = list.find(p => p.code === changeResult.provinceCode).areaList
        // 当你点击市级信息的时候,显示区一级的信息
        if (changeResult.cityCode && changeResult.cityName) {
          list = list.find(c => c.code === changeResult.cityCode).areaList
        }
      }

      return list
    })
    // 定义选择的省市区数据
    const changeResult = reactive({
      provinceCode: '',
      provinceName: '',
      cityCode: '',
      cityName: '',
      countyCode: '',
      countryName: '',
      fullLocation: ''
    })
    // 当点击按钮时记录
    const changeItem = (item) => {
      if (item.level === 0) {
        // 省级
        changeResult.provinceCode = item.code
        changeResult.provinceName = item.name
      }
      if (item.level === 1) {
        // 市级
        changeResult.cityCode = item.code
        changeResult.cityName = item.name
      }
      if (item.level === 2) {
        // 区级
        changeResult.countyCode = item.code
        changeResult.countryName = item.name
        changeResult.fullLocation = `${changeResult.provinceName} ${changeResult.cityName} ${changeResult.countryName}`
        // 这是最后一级,关闭对话框
        close()

        // 通知父组件
        emit('change', changeResult)
      }
    }

    return {
      visible,
      toggle,
      target,
      loading,
      currList,
      changeItem
    }
  }

}
// 获取省市区数据函数
const getCityData = () => {
  // 数据：https://yjy-oss-files.oss-cn-zhangjiakou.aliyuncs.com/tuxian/area.json
  // 1.当本地没有缓存，发请求获取数据
  // 2.当本地有缓存，去除本地数据
  // 返回promise在then获取数据，这里可能是异步操作可能是同步操作
  return new Promise((resolve, reject) => {
    // 约定：数据储存在window上cityData字段
    // 本地有存储
    if (window.cityData) {
      resolve(window.cityData)
    } else { // 本地没缓存
      const url = 'https://yjy-oss-files.oss-cn-zhangjiakou.aliyuncs.com/tuxian/area.json'
      axios.get(url).then(res => {
        // 缓存
        window.cityData = res.data
        resolve(res.data)
      })
    }
  })
}
</script>
<style scoped lang="less">
.xtx-city {
  display: inline-block;
  position: relative;
  z-index: 400;
  .select {
    border: 1px solid #e4e4e4;
    height: 30px;
    padding: 0 5px;
    line-height: 28px;
    cursor: pointer;
    &.active {
      background: #fff;
    }
    .placeholder {
      color: #999;
    }
    .value {
      color: #666;
      font-size: 12px;
    }
    i {
      font-size: 12px;
      margin-left: 5px;
    }
  }
  .option {
    width: 542px;
    border: 1px solid #e4e4e4;
    position: absolute;
    left: 0;
    top: 29px;
    background: #fff;
    min-height: 30px;
    line-height: 30px;
    display: flex;
    flex-wrap: wrap;
    padding: 10px;
    > span {
      width: 130px;
      text-align: center;
      cursor: pointer;
      border-radius: 4px;
      padding: 0 3px;
      &:hover {
        background: #f5f5f5;
      }
    }
    // 加载中
    .loading {
      height: 290px;
      width: 100%;
      background: url(../../assets/images/loading.gif) no-repeat center;
    }
  }
}
</style>
